export default {
  name: 'mt-fixed-button',
    methods: {
        handleClick(evt) {
            this.$emit('click', evt);
        }
    },
  props: {
    position: {
      type: String,
      default: 'bottom-right'
    },
    type: {
        type: String,
        default: 'primary'
    },
    buttonSize: String,
    contentSize: String,
    color: String,
    backgroundColor: String,
    left: String,
    right: String,
    top: String,
    bottom: String
  },
    computed: {
        computeStyle: function() {
            let left = this.left;
            let right = this.right;
            let top = this.top;
            let bottom = this.bottom;
            let backgroundColor = this.backgroundColor;
            let buttonSize = this.buttonSize;
            let color = this.color;
            let _style = '';

            if (left) {
                _style += 'left:' + left + ';';
            }
            if (right) {
                _style += 'right:' + right + ';';
            }
            if (top) {
                _style += 'top:' + top + ';';
            }
            if (bottom) {
                _style += 'bottom:' + bottom + ';';
            }

            if (backgroundColor) {
                _style += 'background-color:' + backgroundColor + ';';
            }
            if (buttonSize) {
                _style += 'width:' + buttonSize + ';';
                _style += 'height:' + buttonSize + ';';
            }

            return _style;
        },
        computePlusStyle: function() {
            let contentSize = this.contentSize;
            let color = this.color;
            let plusStyle = '';
            if (contentSize) {
                plusStyle += 'width:' + contentSize + ';';
                plusStyle += 'height:' + contentSize + ';';
            }

            return plusStyle;
        },
        computePlusItemStyle: function() {
            let color = this.color;
            let plusStyle = '';
            if(color){
                plusStyle += 'background-color:' + color + ';';
            }

            return plusStyle;
        }
    }
};



